module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-popover-control',\n  template: `\n    <button\n      nz-button\n      nzType=\"primary\"\n      nz-popover\n      nzTitle=\"Title\"\n      [(nzVisible)]=\"visible\"\n      (nzVisibleChange)=\"change($event)\"\n      nzTrigger=\"click\"\n      [nzContent]=\"contentTemplate\">\n      Click me\n    </button>\n    <ng-template #contentTemplate>\n      <a (click)='clickMe()'>Close</a>\n    </ng-template>\n  `\n})\nexport class NzDemoPopoverControlComponent {\n  visible: boolean;\n\n  clickMe(): void {\n    this.visible = false;\n  }\n\n  change(value: boolean): void {\n    console.log(value);\n  }\n}\n"